html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  -webkit-overflow-scrolling: touch;
}

#page {
  opacity: 0;
  transition-property: opacity;
  transition-duration: 1.5s;
}

.main-header-menu > .menu-item {
  margin: 0 0 0 8px;
}
.main-header-menu > .menu-item > .menu-link {
  background-color: rgba(255, 255, 255, 0.5);
}

#ast-hf-mobile-menu {
  padding-top: 1px;
}
#ast-hf-mobile-menu > .menu-item {
  margin: 0 0 0 0;
}

.ast-mobile-header-content .ast-builder-menu-mobile .ast-main-header-bar-alignment .main-header-bar-navigation .site-navigation .main-navigation .main-header-menu .menu-item a {
  text-align: center;
}
.ast-mobile-header-content .ast-builder-layout-element {
  line-height: 3;
  text-align: center;
}
.ast-mobile-header-content .ast-builder-layout-element .ast-header-social-wrap {
  width: 100%;
}
.ast-mobile-header-content .ast-builder-layout-element .ast-header-social-wrap .header-social-inner-wrap:hover {
  background-color: var(--ast-global-color-4);
}
.ast-mobile-header-content .ast-builder-layout-element .ast-header-social-wrap .header-social-inner-wrap a {
  width: 100%;
  height: 100%;
  padding: 0 20px;
}

@media (max-width: 544px) {
  .wp-block-columns .wp-block-column:not(:last-child) {
    margin-bottom: 0;
  }
}
.grecaptcha-badge {
  bottom: 80px !important;
  z-index: 999;
}

.pgcal-container {
  padding: 16px;
  background-color: #fff;
}

.ast-theme-transparent-header .post-password-form {
  margin-top: 160px;
  max-width: 300px;
}
.ast-theme-transparent-header .post-password-form label input[name=post_password] {
  margin: 4px auto 0;
  width: 200px;
  display: block;
  text-align: center;
}
.ast-theme-transparent-header .post-password-form input[type=submit] {
  margin: 32px 0 0 0;
}

.wp-block-group.slider > .wp-block-group {
  position: relative;
  margin-bottom: 0;
  overflow: hidden;
}
.wp-block-group.slider > .wp-block-group .flickity-viewport {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100% !important;
  height: 100% !important;
}
.wp-block-group.slider > .wp-block-group .flickity-viewport .flickity-slider .wp-block-cover {
  width: 100% !important;
}
.wp-block-group.slider > .slidertitle {
  position: absolute;
  top: 65%;
  left: 50%;
  width: fit-content;
  transform: translateX(-50%) translateY(-50%);
  border-radius: 4px;
}

#LEDS-Reservas {
  position: relative;
  padding: 16px;
  background-color: var(--ast-global-color-4);
  border-radius: 4px;
  overflow: hidden;
}
#LEDS-Reservas table {
  margin: 0;
}
#LEDS-Reservas .fc-view-harness {
  background-color: #fff;
}
#LEDS-Reservas .fc-day {
  cursor: pointer;
}
#LEDS-Reservas #FormWrapper {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 9999;
  text-align: center;
}
#LEDS-Reservas #FormWrapper:before, #LEDS-Reservas #FormWrapper:after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 0;
  vertical-align: middle;
}
#LEDS-Reservas #FormWrapper #Form {
  display: inline-block;
  vertical-align: middle;
}
#LEDS-Reservas #Form {
  max-width: 320px;
  margin: 0 auto;
  padding: 24px;
  background-color: #fff;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.6);
  border-radius: 4px;
  text-align: left;
  line-height: 1.3em;
}
#LEDS-Reservas #Form > .Title {
  margin: 0 0 24px 0;
  padding: 0 32px 0 0;
  position: relative;
  min-height: 16px;
}
#LEDS-Reservas #Form > .Title .TitleText {
  flex: 1;
  line-height: 1.1em;
  font-weight: bold;
  font-size: 1.2em;
  text-align: left;
}
#LEDS-Reservas #Form > .Title .Close {
  position: absolute;
  top: -12px;
  right: -12px;
  padding-top: 7px;
  padding-right: 12px;
  padding-bottom: 9px;
  padding-left: 12px;
}
#LEDS-Reservas #Form > .Title .Close h3 {
  line-height: 1;
  color: #fff;
  margin-bottom: 0;
}
#LEDS-Reservas #Form .Text,
#LEDS-Reservas #Form .ErrorMessage,
#LEDS-Reservas #Form .Message {
  text-align: center;
}
#LEDS-Reservas #Form .ErrorMessage {
  color: #cc0000;
  font-size: 0.9em;
  font-weight: bold;
}
#LEDS-Reservas #Form .Fields {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
#LEDS-Reservas #Form .Fields .Field {
  width: 100%;
  display: flex;
  align-items: center;
}
#LEDS-Reservas #Form .Fields .Field select {
  width: 100%;
  padding: 0 0 4px 0 !important;
  border-width: 0 0 1px 0 !important;
  border-radius: 0;
  cursor: pointer;
  height: auto !important;
  font-size: 0.95em;
}
#LEDS-Reservas #Form .Fields .Field select:disabled {
  opacity: 0.2;
}
#LEDS-Reservas #Form .Fields .Field.HourSelector {
  width: calc(45% - 8px);
}
#LEDS-Reservas #Form .Fields .Field.DurationSelector {
  width: calc(55% - 8px);
}
#LEDS-Reservas #Form .Fields .Field.Recurrent {
  width: 100%;
  margin: 16px 0 0 0;
  align-items: stretch;
}
#LEDS-Reservas #Form .Fields .Field.Recurrent input {
  width: 16px;
  height: 16px;
  margin: 2px 6px 0 0;
  cursor: pointer;
}
#LEDS-Reservas #Form .Fields .Field.Recurrent label {
  flex: 1;
  font-size: 0.95em;
  cursor: pointer;
}
#LEDS-Reservas #Form .Fields .Field.Durante {
  width: 100%;
  margin: 12px 0 0 0;
}
#LEDS-Reservas #Form .Fields .Field.Title {
  margin: 16px 0 0 0;
}
#LEDS-Reservas #Form .Fields .Field.Title input {
  width: 100%;
  padding: 0 0 4px 0 !important;
  border-width: 0 0 1px 0 !important;
  border-radius: 0;
  height: auto !important;
}
#LEDS-Reservas #Form .Fields .Field.Mail {
  margin: 24px 0 0 0;
}
#LEDS-Reservas #Form .Fields .Field.Mail input {
  width: 100%;
  padding: 0 0 4px 0 !important;
  border-width: 0 0 1px 0 !important;
  border-radius: 0;
  cursor: pointer;
  height: auto !important;
  text-align: center;
}
#LEDS-Reservas #Form .Actions {
  margin: 16px 0 0 0;
  text-align: center;
}
#LEDS-Reservas #Form .Actions button {
  width: 90%;
  opacity: 1;
  transition-property: opacity;
  transition-duration: 1s;
}
#LEDS-Reservas #Form .Actions button:disabled {
  opacity: 0.2;
  cursor: not-allowed;
}
#LEDS-Reservas #Form .Actions button + button {
  margin: 16px 0 0 0;
}

body.WithBGRotatory #masthead {
  z-index: 2;
}
body.WithBGRotatory #content {
  z-index: 1;
}
body.WithBGRotatory #colophon {
  z-index: 2;
}

@keyframes rotatory {
  from {
    transform: translateX(-50%) translateY(-50%) rotate(0deg);
  }
  to {
    transform: translateX(-50%) translateY(-50%) rotate(359deg);
  }
}
#LEDS-BG-Rotatory {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 0;
}
#LEDS-BG-Rotatory .ImageA,
#LEDS-BG-Rotatory .ImageB {
  position: absolute;
  width: 3000px;
  height: 3000px;
  opacity: 0.15;
  background-image: url("~assets/images/color-gigante-a.jpg");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  transform-origin: center;
  transform: translateX(-50%) translateY(-50%) rotate(10deg);
  animation-name: rotatory;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
#LEDS-BG-Rotatory .ImageA {
  top: 0;
  left: 0;
  animation-duration: 30s;
}
#LEDS-BG-Rotatory .ImageB {
  top: 100%;
  left: 100%;
  animation-duration: 20s;
}

